////
/// @group components
////

/// Button
/// @since 0.1.0
.btn.btn { // Repeat selector for specificity to avoid link overrides
  @include remove-mz-focus-inner;
  @include font-size(0);
  appearance: none;
  background: $colour-btn-primary;
  border: em(1px) solid $colour-btn-primary-text;
  border-radius: 0;
  color: $colour-btn-primary-text;
  cursor: pointer;
  display: inline-block;
  margin: em(0 $spacing-small $spacing-small -4px); // Negative left margin to combat inline-block gaps
  padding: em($spacing-small $spacing-medium);
  position: relative;
  text-decoration: none;
  vertical-align: top;

  &:first-child {
    margin-left: 0;
  }

  &:visited {
    color: $colour-btn-primary-text;
  }

  &:hover {
    background: $colour-btn-primary-hover;
    color: $colour-btn-primary-text;
    text-decoration: none;
  }

  &:focus {
    @include default-focus-style;
    background: $colour-btn-primary;
    border-color: $colour-focus;
  }

  &:active {
    background: $colour-btn-primary-hover;
    text-decoration: none;
    top: em(2px);
  }

  // Disabled button styles
  &--disabled,
  &.disabled,
  &[aria-disabled='true'],
  &[disabled='disabled'],
  &[disabled] {
    opacity: .5;
    pointer-events: none;

    &:hover {
      cursor: default;
    }
  }
}

/// CTA/prominent button
/// @since 0.2.0
.btn.btn--cta {
  background: $colour-btn-cta;
  border: em(1px) solid $colour-btn-cta-text;
  color: $colour-btn-cta-text;

  &:visited {
    color: $colour-btn-cta-text;
  }

  &:hover {
    background: $colour-btn-cta-hover;
    color: $colour-btn-cta-text;
  }

  &:focus {
    background: $colour-btn-cta-hover;
  }

  &:active {
    background: $colour-btn-cta-hover;
  }
}

